/* Styling for main .App container */
.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 90vh;
  width: 100vw;
}

/* Styling for Global Elements */
code {
  background: #000000;
  color: rgb(18, 186, 236);
}

blockquote {
  background: #bbb8b8;
  color: red;
}

img {
  height: 25%;
  width: 25%;
}

/* Styling by class */
.top {
  width: 100%;
  text-align: center;
}

.editor {
  width: 45%;
}

.preview {
  width: 45%;
}

/* Styling by id  */
#editor {
  height: 90%;
  width: 100%;
  border: 5px solid #000000;
  font-size: 16px;
  padding: 5px;
}

